.ShowDetails {
    display: flex;
    margin-left: 150px;
    margin-right: 150px;
    margin-top: 30px;
    padding: 20px;
    border: 1px solid #eee;
    box-shadow: 0 2px 3px #ccc;
    justify-content: center;


    .Detailscontainer {
        display: flex;
        flex-wrap: wrap;

        p {
            font-size: 20px;
        }
    }

    .Imagecontainer {
        img {
            padding-left: 20px;
        }
    }
    
    .Episodelink {
        font-size: x-large;
    
        ul > li {
            font-size: large;
        }
    }
}